
:root{
  --p60-percent:#D9E6CC     ; /*BG*/
  --p30-percent:#479D5B; /*main elements*/
  --p10-percent:#013B4D; /*attention*/
  --simple-white: #e3e4e2cb;
  --simple-black: #001116d2;

  --link-color: var(--p30-percent);
  /* Light theme */
  --primary-light: var(--p60-percent);
  --secondary-light: var(--p30-percent);
  --accent-light: var(--p10-percent);
  --text-color-light: var(--simple-black);

  /* Dark theme */
  --primary-dark: var(--p10-percent);
  --secondary-dark: var(--p30-percent);
  --accent-dark: var(--p60-percent);
  --text-color-dark: var(--simple-white);

  /* Font Size */
  --h1-size: 80px;
  --h2-size: 40px;
  --h3-size: 32px;
  --h4-size: 24px;
  --h5-size: 24px;
  --h6-size: 16px;
  --paragraph-size: 24px;
  --fonts-famillies : 'Inconsolata';
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html{
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-size: larger;
  font-family: var(--fonts-famillies);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--text-color-light);
}

.flex-c{
  display: flex;
  flex-direction: column;
}
.flex-r{
  display: flex;
  flex-direction: row;
}
.jc-sb{
  justify-content: space-between;
}
.jc-sa{
  justify-content: space-around;
}
a{
  text-decoration: none;
  color: var(--link-color);
  font-weight: bold;
}
a:hover{
  color: var(--text-color-light);
}
.btn{
  display: block;
  min-width: fit-content;
  padding: 16px 32px;
  text-align: center;
  border-radius: 25px 25px;
}
.btn:hover{
  cursor: pointer;
}
.btn-primary{
  background-color: var(--primary-light)!important;
  color: var(--text-color-light);
  border: 1px solid var(--secondary-light);
}
.btn-primary:hover{
  background-color: var(--secondary-light)!important;
}
.btn-secondary{
  background-color: var(--secondary-light)!important;
  color: var(--text-color-light);
}
.btn-secondary:hover{
  background-color: var(--primary-light)!important;
  border: 1px solid var(--secondary-light);
}
.btn-special{
  background-color: var(--accent-light)!important;
  color: var(--text-color-dark);
}
.btn-special:hover{
  color: var(--text-color-light);
  background-color: var(--primary-light)!important;
  border: 1px solid var(--accent-light);
}

li{
  text-decoration: none;
  list-style-type: none;
}


.p8{
  padding: 8px;
}
.p16{
  padding: 16px;
}
.p32{
  padding: 16px;
}
.align-center{
  /* text-align: center; */
  align-items: center;
  /* align-self: center; */
}

.active{
  padding: 16px ;
  background-color: var(--secondary-light)!important;
  color: var(--text-color-light);
}
.scroll-y{
 overflow-y:scroll!important;
}
.container{
  padding: 8px;
  margin: 8px;
}
svg{
  color: var(--p10-percent);
}