@import url(./index.css);

.main-layout{
    background-color:  var(--primary-light);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.header{
    position: fixed;
    top: 0;
    width: 100%;
    background-color: var(--primary-light);
}
.bodylayout{
    margin-top: 100px;
    overflow: auto;
    /* border: 1px solid red; */
}

.socials{
    
    position: absolute;
    display: flex;
    flex-direction: column-reverse;;
    bottom: 5%;
    right: 1.2%;
    height: 70%;
    width: 70px;
    justify-content: space-evenly;
}
