@import url('./index.css');

.nav-links .sidebar{
    padding: 16px;
}
.hidable{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-width: 60vw;
}
.is-hide{
    left: 0;
    display: none;
    cursor: pointer;
}
.break{
    display: none;
}

@media only screen and (max-width:933px) {
  .is-hide{
    left: 0;
    display: flex!important;
    cursor: pointer;
    flex-direction: row-reverse;
    align-content: flex-end!important;
  }
  .hidable{
    flex-direction: column;
    display: none;
  }
  .not-hidable{
    flex-direction: column;
    width: 100%;
    height: 400px;
    transition: height 2s ease-in-out;

  }
  .nav-links{
    /* display: none; */
    flex-direction: column;
  }
  .nav-links .sidebar .custom{
    display: block;
    width: 100%;
}
  .break{
    display: block;
  }
  .nav-button{
    /* display: none; */
    /* flex-direction: column; */
  }


}